const enemy = [
  {"name": "ÂèØÁà±Êú±ÂâçÁî∑Âèã", "imgUrl": "/assets/img/ex.jpg", "weight": 10, "good": false}, 
  {"name": "ÂèØÁà±Êú±ËÑæÊ∞îÂêêÊßΩËÄÖ", "imgUrl": "/assets/img/tucao.jpg", "weight": 2, "good": false}, 
  {"name": "Â´âÂ¶íÂèØÁà±Êú±È¢úÂÄºÁöÑ‰∫∫", "imgUrl": "/assets/img/uglywomen.jpg", "weight": 1, "good": false},
  {"name": "ÂèØÁà±Êú±ÂÆàÊä§ËÄÖ", "imgUrl": "/assets/img/178.jpg", "weight": 1, "good": true},
  ]
const weapons = ["üó°Ô∏è", "üî™", "üî´"]
const credits = [10, 5];
const t = 30;
window.onload = function() {
  new Vue({
    el: "#app",
    data: {
      health: 100,
      ended: false,
      // prettier-ignore
      batman: [
        "AIEEE!!!!", "AIIEEE!!!!", "ARRGH!!!!", "AWK!!!!", "AWKKKKKK!!!!", "BAM!!!!", "BANG!!!!", "BANG - ETH!!!!", "BIFF!!!!", "BLOOP!!!!", "BLURP!!!!", "BOFF!!!!", "BONK!!!!", "CLANK!!!!", "CLANK - EST!!!!", "CLASH!!!!", "CLUNK!!!!", "CLUNK - ETH!!!!", "CRRAACK!!!!", "CRASH!!!!", "CRRAACK!!!!", "CRUNCH!!!!", "CRUNCH - ETH!!!!", "EEE - YOW!!!!", "FLRBBBBB!!!!", "GLIPP!!!!", "GLURPP!!!!", "KAPOW!!!!", "KAYO!!!!", "KER - SPLOOSH!!!!", "KERPLOP!!!!", "KLONK!!!!", "KLUNK!!!!", "KRUNCH!!!!", "OOOFF!!!!", "OOOOFF!!!!", "OUCH!!!!", "OUCH - ETH!!!!", "OWWW!!!!", "OW - ETH!!!!", "PAM!!!!", "PLOP!!!!", "POW!!!!", "POWIE!!!!", "QUNCKKK!!!!", "RAKKK!!!!", "RIP!!!!", "SLOSH!!!!", "SOCK!!!!", "SPLATS!!!!", "SPLATT!!!!", "SPLOOSH!!!!", "SWAAP!!!!", "SWISH!!!!", "SWOOSH!!!!", "THUNK!!!!", "THWACK!!!!", "THWACKE!!!!", "THWAPE!!!!", "THWAPP!!!!", "UGGH!!!!", "URKKK!!!!", "VRONK!!!!", "WHACK!!!!", "WHACK - ETH!!!!", "WHAM - ETH!!!!", "WHAMM!!!!", "WHAMMM!!!!", "WHAP!!!!", "Z - ZWAP!!!!", "ZAM!!!!", "ZAMM!!!!", "ZAMMM!!!!", "ZAP!!!!", "ZAP - ETH!!!!", "ZGRUPPP!!!!", "ZLONK!!!!", "ZLOPP!!!!", "ZLOTT!!!!", "ZOK!!!!", "ZOWIE!!!!", "ZWAPP!!!!", "ZZWAP!!!!", "ZZZZWAP!!!!", "ZZZZZWAP!!!!"
      ],
      time: t,
      chosenWord: "",
      weapons: ["üó°Ô∏è", "üî™", "üî´"],
      credits: [10, 5],
      total: 0,
      timer: null,
      remainTimer: null,
      curEnemy: enemy[Math.floor(Math.random()*enemy.length)],
      neededWeaponIndex: 0,
      uselessWeaponIndex: 1,
      remainTime: t,
      earnedCredit: 0,
    },
    mounted() {
      if (this.timer) {
        clearInterval(this.timer);
      } else {
        this.timer = setInterval(this.updateEnemy, 1500);
      }
      if (this.remainTimer) {
        clearInterval(this.tiktoc);
      } else {
        this.timer = setInterval(this.tiktoc, 1000);
      }
    },
    methods: {
      tiktoc(){
        if (this.remainTime == 0){
          this.ended = true;
        }
        if (!this.ended){
          this.remainTime--;
        }
      },
      updateEnemy() {
        if (!this.ended){
          this.curEnemy = enemy[Math.floor(Math.random()*enemy.length)];
          this.neededWeaponIndex = Math.floor((Math.random()*3));
          this.uselessWeaponIndex = (this.neededWeaponIndex+1)%3;
        }
      },
      attack: function(weapon){
        this.earnedCredit = 0;
        var fainted = new Audio("/assets/sounds/fainted.mp3");
        if (this.curEnemy.good){
          this.earnedCredit = -this.curEnemy.weight*credits[0];
        } else {

          if (weapon == this.uselessWeaponIndex){
            this.earnedCredit = -this.curEnemy.weight*credits[1];
          }else if (weapon == this.neededWeaponIndex){
            this.earnedCredit = this.curEnemy.weight*credits[0];
          } else {
            this.earnedCredit = this.curEnemy.weight*credits[1];
          }
          if (weapon == 0){
            var knockout = new Audio("/assets/sounds/knockout.wav");
            knockout.play();
          } else if (weapon == 1){
            var punch = new Audio("/assets/sounds/punch.mp3");
            punch.play();
          } else {
            fainted.play();
            var knockout = new Audio("/assets/sounds/knockout.wav");
          }
        }

        this.total += this.earnedCredit;
        console.log(this.total);
        this.updateEnemy();
      },
      punch: function() {
        this.health -= 10;
        if (this.health <= 0) {
          this.ended = true;
          this.playKnockout();
        }
      },
      restart: function() {
        this.remainTime = t;
        this.ended = false;
        this.chosenWord = "";
        this.total = 0;
        this.earnedCredit = 0;
      },
      playPunch: function() {
        var punch = new Audio("/assets/sounds/punch.mp3");
        punch.play();
      },
      playKnockout: function() {
        var knockout = new Audio("/assets/sounds/knockout.wav");
        var fainted = new Audio("/assets/sounds/fainted.mp3");
        fainted.play();
        knockout.play();
      },
      playSegway: function() {
        var segway = new Audio("/assets/sounds/segway.mp3");
        segway.play();
      },
      pickWords: function() {
        this.chosenWord = this.batman[
          Math.floor(Math.random() * this.batman.length)
        ];
      },
      beforeDestroy() {
        clearInterval(this.timer);
      },
    }
  });
};
